
.midTemp2-cover h1{
	font-size: 4vw;
	font-weight: bold;
	font-family: 'Poppins',sans-serif;
	margin-bottom: 0px;
}
.midTemp2-cover h2{
	font-size: 1.8vw;
	font-family: sans-serif;
	margin-bottom: 1.2vw;
}
.midTemp2-cover h4{
	font-weight: 600;
	font-size: 2vw;
	border-bottom: .1vw solid black;
	margin-top: 2vw;
	padding-bottom: .3vw;
	font-family: sans-serif;
}
.midTemp2-cover h5{
	font-size: 1.6vw;
	margin: 0px;
}
.midTemp2-cover span{
	font-weight: bold;
	font-weight: 600;
	font-size: 1.5vw;
	font-family: 'Poppins',sans-serif;
}
.midTemp2-cover p{
	font-size: 1.4vw;
	text-align: justify;
	font-family: 'Poppins',sans-serif;
}
.midTemp2-cover ul li{
	font-size: 1.4vw;
	padding: 0px;
	font-family: 'Poppins',sans-serif;
}
.midTemp2-cover table tr td{
	font-size: 1.4vw;
	font-family: 'Poppins',sans-serif;
}

.midTemp2-cover .contact-list-icons{
	margin-right: .3vw;
	font-family: 'Poppins',sans-serif;
	font-size: 1.4vw;
}

.midTemp2-cover .skills-list{
	padding-left: 1.5vw;
}

.midTemp2-cover .interest-list{
	padding-left: 1.5vw;
}

.midTemp2-cover .education-list-icons{
	margin-right: .5vw;
}



/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
  .modal-dialog.modal-xl{
  	max-width: 98%;
  	margin:3vw auto;
  }
  
  .midTemp2-cover h1{
	font-size: 6.1vw;
	}
	.midTemp2-cover h2{
		font-size: 3vw;
	}
	.midTemp2-cover h4{
		font-size: 3.2vw;
		margin-top: 2.2vw;
		border-bottom: .05vw solid black;
		padding-bottom: .3vw;
		margin-bottom: .6vw;
	}
	.midTemp2-cover h5{
		font-size: 2.8vw;
	}
	.midTemp2-cover span{
		
		font-size: 2.6vw;

	}
	.midTemp2-cover p{
		font-size: 2.4vw;
		margin-bottom: 1vw;
	}
	.midTemp2-cover ul{
		padding-left: 3vw;
		margin-bottom: 0vw;
	}
	.midTemp2-cover ul li{
		font-size: 2.4vw;
		padding: 0px;
	}
	.midTemp2-cover table tr td{
		font-size: 2.4vw;
	}
	
	.midTemp2-cover .contact-box{
		text-align: left;
		font-size: 2.4vw;
		margin: 1.9vw 0vw;
	}
	.midTemp2-cover .contact-list-icons{
	margin-right: .8vw;
	font-size: 2.4vw;
	}

	.midTemp2-cover ul.skills-list{
		padding-left: 1.5vw;
		margin-bottom: 1vw;

	}

	.midTemp2-cover ul.interest-list{
		padding-left: 1.5vw;
		margin-bottom: 1vw;

	}

	.midTemp2-cover .education-list-icons{
		margin-right: .5vw;
	}

}


